
import React, { useEffect, useState } from 'react'
import axios from 'axios'
import ReuploadHoardingDocTable from './ReuploadHoardingDocTable';
import AdvertisementApiList from '../../../AdvertisementApiList';
import ApiHeaderAdvrt from '../../../ApiHeaderAdvrt';
import AxiosInterceptors from '../../../../../Components/GlobalData/AxiosInterceptors';

function ReuploadHoardingDocModal(props) {

    const { api_getHoardingReuploadDoc } = AdvertisementApiList()
    const [docList, setdocList] = useState()

    ///////////{*** GET DOCUMENT LIST***}/////////
    useEffect(() => {
        getDocumentList()
    }, [])
    const getDocumentList = () => {
        const requestBody = {
            applicationId: props?.data,
            // workflowId: props?.workflowId,
        }
        AxiosInterceptors.post(`${api_getHoardingReuploadDoc}`, requestBody, ApiHeaderAdvrt())
            .then(function (response) {
                console.log('re-uploaded document list', response.data.data)
                setdocList(response.data.data)
            })
            .catch(function (error) {
                console.log('errorrr.... ', error);
            })
    }
    console.log("application id", props.data)
    console.log("workflow id 1", props.workflowId)
    return (
        <>
            <div className=' top-4 ml-4 '>
                <div className=' grid grid-cols-1 md:grid-cols-12 lg:grid-cols-12 gap-4 container mx-auto pb-8 p-2 mt-12'>
                    <div className='col-span-12'><h1 className='text-xl'>Upload Rejected Document</h1></div>
                    <div className='col-span-12 border border-dashed border-violet-800 '>
                        <div className="p-1">
                            <div class="grid grid-cols-1 md:grid-cols-5 lg:grid-cols-5 gap-1 p-1 bg-white">
                                <div>
                                    <h1 className='text-left  text-lg ml-12 text-gray-600 font-sans font-semibold '>Document</h1>
                                </div>
                                <div>
                                    <h1 className='text-center text-lg ml-4 text-gray-600 font-sans font-semibold'>Preview</h1>
                                </div>
                                <div>
                                    <h1 className='text-center text-lg ml-4 text-gray-600 font-sans font-semibold'>Upload</h1>
                                </div>
                                <div>
                                    <h1 className='text-center text-lg ml-4 text-gray-600 font-sans font-semibold'>Status</h1>
                                </div>
                                <div>
                                    <h1 className='text-center text-lg ml-4 text-gray-600 font-sans font-semibold'>Action</h1>
                                </div>
                            </div>
                            <div className='mt-2'>
                                {docList?.map((items, index) => (
                                    <ReuploadHoardingDocTable data={items} mykey={index} />
                                ))}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default ReuploadHoardingDocModal