import { useEffect, useState } from "react";
import { useQuery } from "react-query";
import axios from "axios";
import { format } from "date-fns";
import Modal from 'react-modal';
import HoardingApplicationFullDetail from "./HoardingApplicationFullDetail";
import Loader from "../../Loader";
import AgencyNotification from "../AgencyNotification";
import BackToAgency from "../../BackToAgency";
import AdvertisementApiList from "../../../AdvertisementApiList";
import ApiHeaderAdvrt from "../../../ApiHeaderAdvrt";
import ListTableAdvert from "../../../ListTableAdvert";
import ErrorPage from "../../../ErrorPage";
import ReuploadHoardingDocModal from "./ReuploadHoardingDocModal";
import BrandLoader from "../../../../../Components/Common/BrandLoader";
import CommonModal from "../../../../../Components/GlobalData/CommonModal";
import ServerErrorCard from "../../../ServerErrorCard";
import AxiosInterceptors from "../../../../../Components/GlobalData/AxiosInterceptors";
import ApiHeader from "../../../../../Components/ApiList/ApiHeader";
import AdvtListTablePagination from "../../../AdvtListTablePagination";




const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        backgroundColor: 'white',
        border: 'none'
    },
};
Modal.setAppElement('#root');

function ViewAllHoardingPendingList(props) {
    const [tableState, setTableState] = useState(false);
    const [listStatus, setlistStatus] = useState(false);
    const { api_getHoardingPendingApplicationList } = AdvertisementApiList()
    const [applicationDetail, setapplicationDetail] = useState()

    const [applicationType, setapplicationType] = useState()
    const [applicationNo, setapplicationNo] = useState()
    const [show, setshow] = useState(false)
    const [tableStatus, settableStatus] = useState(false);
    // const [isLoading, setisLoading] = useState(false);
    const [erroState, seterroState] = useState(false);

    const [modalIsOpen, setIsOpen] = useState(false);
    const [modalIsOpen1, setIsOpen1] = useState(false);
    const openModal = () => setIsOpen(true)
    const closeModal = () => setIsOpen(false)
    const afterOpenModal = () => { }
    const openModal1 = () => setIsOpen1(true)
    const closeModal1 = () => setIsOpen1(false)
    const afterOpenModal1 = () => { }

    const [documentList, setdocumentList] = useState()



    ///testing pagination
    const [readymadeListData, setreadymadeListData] = useState(false);
    const [readymadeListStatus, setreadymadeListStatus] = useState(false);
    const [isLoading, setisLoading] = useState(false);

    // table related states
    const [totalDataCount, settotalDataCount] = useState(0);
    const [currentPageIndex, setcurrentPageIndex] = useState(1);
    const [lastPageIndex, setlastPageIndex] = useState(0);
    const [pageLength, setpageLength] = useState(10);
    const [searchValue, setsearchValue] = useState('');
    const [refreshStatus, setrefreshStatus] = useState(false);

    const showLoader = (val) => {
        setshow(val);
    }



    const COLUMNS = [
        {
            Header: "#",
            // accessor: 'ward_no'
            Cell: ({ row }) => <div className="pr-2">{row.index + 1}</div>,
        },
        {
            Header: "Application No",
            accessor: "application_no",
        },
        {
            Header: "Apply Date",
            accessor: "application_date",
        },
        // {
        //     Header: "Payment Amount",
        //     accessor: "payment_amount",
        // },
        // {
        //     Header: "Firm Name",
        //     accessor: "bank_name",
        // },
        {
            Header: "Action",
            accessor: "id",
            Cell: ({ cell }) => (
                <>
                    <button
                        onClick={() => modalAction(cell.row.values.id, "Active")}
                        className="bg-indigo-500 text-xs px-3 py-1 rounded leading-5 shadow-lg hover:shadow-xl hover:bg-indigo-700 
                hover:text-white text-white"
                    >
                        View
                    </button>
                    {cell.row.original.doc_upload_status == 0 &&
                        <button
                            onClick={() => docUploadModal(cell.row.values.id)}
                            className="bg-indigo-500 text-xs ml-4 px-3 py-1 rounded leading-5 shadow-lg hover:shadow-xl hover:bg-indigo-700
                        hover:text-white text-white"
                        >
                            upload Document
                        </button>
                    }
                </>
            ),
        },
    ];

    const modalAction = (applicationId, applicationType) => {
        console.log("..............application id..............", applicationId)
        console.log("..............application type..............", applicationType)
        setapplicationNo(applicationId)
        setapplicationType(applicationType)
        openModal()
    }
    const docUploadModal = (applicationId) => {
        console.log("..............application id..............", applicationId)
        setapplicationNo(applicationId)
        openModal1()
    }

    useEffect(() => {
        fetchInboxList(currentPageIndex, pageLength, searchValue)
    }, [currentPageIndex, searchValue, pageLength])

    const fetchInboxList = (page, perPage, key = '') => {
        // console.log('at inbox list fetch')
        setrefreshStatus(true)
        setisLoading(true)

        AxiosInterceptors.post(api_getHoardingPendingApplicationList, { page: page, perPage: perPage, key: key }, ApiHeader())
            .then((res) => {
                // console.log('inbox list response...', res?.data?.data?.data)
                setreadymadeListData(res.data?.data?.data)
                setreadymadeListStatus(true)
                setisLoading(false)

                settotalDataCount(res.data?.data?.total);
                setlastPageIndex(res.data?.data?.last_page)
            })
            .catch((err) => {
                console.log("Error while fetching Filter Data", err)
                setreadymadeListStatus(false)
                setisLoading(false)

            });

        setTimeout(() => {
            setrefreshStatus(false)
        }, [500])
    }


    const values = {
        totalDataCount: totalDataCount,
        currentPageIndex: currentPageIndex,
        setcurrentPageIndex: setcurrentPageIndex,
        pageLength: pageLength,
        setpageLength: setpageLength,
        lastPageIndex: lastPageIndex,
        setlastPageIndex: setlastPageIndex,
        fetchInboxList: fetchInboxList,
        searchValue: searchValue,
        setsearchValue: setsearchValue,
        refreshStatus: refreshStatus
    }


    // const getApplicationList = () => {
    //     seterroState(false)
    //     setisLoading(true)
    //     AxiosInterceptors.post(api_getHoardingPendingApplicationList, {}, ApiHeaderAdvrt())
    //         .then((res) => {
    //             console.log("res", res.data);
    //             if (res.data.status) {
    //                 setapplicationDetail(res.data.data);
    //                 settableStatus(true);
    //                 setisLoading(false)

    //             } else {

    //             }

    //         })
    //         .catch((err) => {
    //             console.log(err);
    //             seterroState(true)
    //             setisLoading(false)
    //         })
    // }


    // useEffect(() => {
    //     getApplicationList()
    // }, [])

    if (isLoading) {
        return (
            <>
                <BrandLoader />
            </>
        )
    }
    if (erroState) {
        return (
            <CommonModal>
                <ServerErrorCard title="Server is busy" desc="Server is too busy to respond. Please try again later." buttonText="View Dashboard" buttonUrl="/propertyDashboard" />
            </CommonModal>
        )
    }

    return (
        <>
            <div className='container bg-white text-gray-600 h-screen mx-auto mt-16'>
                <div>
                    <div className="grid grid-cols-1 md:grid-cols-12 lg:grid-cols-12 gap-4 container mx-auto pt-4 ">
                        <div className="col-span-12 p-2">
                            <div className="flex flex-row bg-white mb-4 rounded leading-5 shadow-md shadow-violet-100 ">
                                <img src="https://cdn-icons-png.flaticon.com/512/3288/3288006.png" className="h-10 ml-3 mt-2" />
                                <h1 className="text-xl text-gray-700 font-semibold p-4">HOARDING PENDING APPLICATIONS</h1>
                            </div>
                        </div>
                    </div>
                    <div className="grid grid-cols-1 p-2">
                        {/* {tableStatus && tableStatus?.data != null && */}
                        {/* {tableStatus &&
                            <ListTableAdvert
                                assessmentType={false}
                                columns={COLUMNS}
                                dataList={applicationDetail?.data}
                            />
                        } */}
                        {readymadeListStatus && readymadeListData?.length != 0 &&
                            // <ListTable assessmentType={false} columns={columnSchema} dataList={readymadeListData} />
                            <AdvtListTablePagination columns={COLUMNS} dataList={readymadeListData} values={values} />
                        }
                        {
                            readymadeListStatus && readymadeListData?.length == 0 &&
                            <div className="text-xl font-semibold text-red-400 text-center">Data Not Found</div>
                        }


                    </div>
                    <div className="grid grid-cols-1">
                        <Modal
                            isOpen={modalIsOpen}
                            onAfterOpen={afterOpenModal}
                            onRequestClose={closeModal}
                            style={customStyles}
                            contentLabel="Example Modal"
                        >
                            <div class=" rounded-lg shadow-xl border-2 border-gray-50 mx-auto px-0 " style={{ 'width': '80vw', 'height': '100%' }}>
                                <div className='h-[44rem] bg-white overflow-y-auto'>
                                    <HoardingApplicationFullDetail data={applicationNo} applicationType={applicationType} showLoader={showLoader} closeModal={closeModal} />
                                </div>
                            </div>
                        </Modal>
                        <Modal
                            isOpen={modalIsOpen1}
                            onAfterOpen={afterOpenModal1}
                            onRequestClose={closeModal1}
                            style={customStyles}
                            contentLabel="Example Modal"
                        >
                            <div class="mx-auto px-0 overflow-y-auto" style={{ 'width': '70vw', 'height': '100%' }}>
                                <div className='h-[44rem] bg-violet-50 overflow-y-auto '>
                                    <ReuploadHoardingDocModal data={applicationNo} closeModal={closeModal1} />
                                </div>
                            </div>
                        </Modal>
                    </div>
                </div>
            </div>

        </>
    );
}

export default ViewAllHoardingPendingList;